.popup__button {
  width: 100%;
  background-color: #000;
  border: none;
  border-radius: 2px;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  line-height: 1.2;
  margin-top: 40px;
  min-height: 50px;
  transition: opacity 0.2s ease;
}

.popup__button:hover {
  opacity: 0.8;
}
